import React, { PureComponent } from "react";
import QRCode from "react-qr-code";
import { connect } from "react-redux";
import { ServerUrl, TagShopServerUrl, SocialWallServerUrl } from '../../../../constants' 
import { isTagShop, isSocialWall, isSocialEmbed, isEditor, isPreview, isUgcsuit } from '../../../../actions/api'
import { wUrl } from "../../../../utils";
import { onSitePopup } from "../../../../actions/OtherActions";
import './scss/OnsiteUploadButton.scss';

 class OnsiteUploadButton extends PureComponent {

  render() {
    const { SnapUp, id, webId } = this.props;
    var ugcSuite = window.ugcSuite ? { ugcSuite: 1 } : isUgcsuit ? { ugcSuite: 1 } : {}

    const isTagshopWall = !webId && isTagShop ? true : false
    const { QrCode, Form } = SnapUp

    const QRButtonText = QrCode.status || QrCode.text != '' ? QrCode.text : ''
    const QRLink = isTagShop ? `${TagShopServerUrl}/${id}/onsite-upload${isTagshopWall ? '?wall=1' : ''}` : `${isSocialWall && !isSocialEmbed ? SocialWallServerUrl : ServerUrl}/${id}?onsite-upload=true${isUgcsuit}`

    const wallQRClass = isSocialWall && !isSocialEmbed ? QRButtonText.length < 31 ? 'tb_snap_btn_wall tb_snap_btn_wall_v' : 'tb_snap_btn_wall' : ''
    const position = QrCode?.position ? ` tb_pos-${QrCode.position}` : null
    return Form.status && (isTagShop || Form.status) && (QrCode.status == 1 || QRButtonText !== '') ? <div className={`tb_onsite_upload_btn_wrap ${wallQRClass}${position}`}>
      <div className="tb_onsite_upload_btn">
        <div className="tb_snap_btn_bg" style={{ opacity: isSocialWall ? 0.7 : null }}><div></div></div>
        <div className='tb_onsite_btn' onClick={() => isEditor || isPreview || wUrl("display.socialwalls.com")  ? this.props.onSitePopup(false) : this.props.onSitePopup(true)}>
          <div className="tb_onsite_qr_btn_wrap">
            {QrCode.status == 1 ? <div className='tb_onsite_qr_code'>
              <QRCode level="L" size={60} value={QRLink} />
            </div> : null}
            {QRButtonText !== '' ?
              <div className={`tb_onsite_btn_txt tb-cTBfont-${QrCode.style.varient}`}>
                {QRButtonText}
              </div> : ''}
          </div>
        </div>
      </div>
    </div> : null
  }
}
const mapStateToProps = state => {
  return {
    modalPop: state.modalPop
  }
}
const mapDispatchToProps = dispatch => {
  return {
    onSitePopup: (status) => dispatch(onSitePopup(status)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(OnsiteUploadButton);