import React, { Component } from 'react'
import OnsiteUploadButton from '../../Components/UserComponents/OnsiteUploadButton'

export default class CardSnapUp extends Component {
       constructor(props) {
              super(props);
              this.state = {
                     snapBtnSize: 0,
                     sizeClass : ''
              };
              this.updateDimensions = this.updateDimensions.bind(this);
       }

       componentDidMount() {
              window.addEventListener('resize', this.updateDimensions);
              this.updateDimensions();
       }

       componentWillUnmount() {
              window.removeEventListener('resize', this.updateDimensions);
       }
       componentDidUpdate() {
              this.updateDimensions();
       }

       updateDimensions() {

              const { className } = this.props
              const snapBtnSize = this.divRef.offsetWidth;

              if (snapBtnSize !== this.state.snapBtnSize) {
                     if(snapBtnSize < 200 && snapBtnSize > 150){
                            this.setState({ sizeClass: `${className}-3x` });
                     }else if(snapBtnSize < 150 && snapBtnSize > 120) {
                            this.setState({ sizeClass: `${className}-2x` });
                     }else if(snapBtnSize < 120 && snapBtnSize > 0) {
                            this.setState({ sizeClass: `${className}-1x` });
                     }
                     this.setState({ snapBtnSize });
              }
       }
       render() {
              const { className, wallId, webId, SnapUp } = this.props
              const { sizeClass } = this.state
              return (
                     <div className={`${className} ${sizeClass}`} ref={(ref) => { this.divRef = ref; }}>
                           <OnsiteUploadButton SnapUp={SnapUp} id={wallId} webId={webId}/>
                     </div>
              )
       }
}